import { NetworkServiceProvider } from './../../providers/network-service/network-service';
import { BasePage } from './../base/base';
import { IonicPage, NavController, NavParams, ToastController, Platform, ModalController } from 'ionic-angular';
import { Component } from '@angular/core';
import { AppServiceProvider, AppGlobal } from '../../providers/app-service/app-service';

/**
 * Generated class for the WamingPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
@IonicPage()
@Component({
  selector: 'page-waming',
  templateUrl: 'waming.html',
})
export class WamingPage extends BasePage {
  taskList: Array<any> = [];
  wamings: string = "waitFor";
  isAndroid: boolean = false;
  status: string = '0';//0 未处理  1已处理
  pageNum: any = 0;
  pageSize: any = 20;
  name: any = '';
  constructor(public navCtrl: NavController,
    public navParams: NavParams,
    public toastCtrl: ToastController,
    public platform: Platform,
    public net: NetworkServiceProvider,
    public modalCtrl: ModalController) {
    super(navCtrl, navParams, toastCtrl)

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WamingPage');
    this.getTaskList();

  }
  clickWaitFor(value) {
    // if (this.platform.is("android")){
    //   this.navCtrl.push("SignInPage");
    // }else{
    //   this.toast("此设备不支持GPS定位");
    // }
    // this.navCtrl.push("SignInPage");  定位
    this.navCtrl.push("TaskDetailsPage", { data: value, wamings: this.wamings });
  }
  clickTaskEdit(value,i) {
    let taskEditModal = this.modalCtrl.create("TaskEditPage", { data: value });
    // this.navCtrl.push("TaskEditPage",{data:value})
    taskEditModal.onDidDismiss(data => {
      //var img = this.siteType(data.siteType)
      if(data.status!='0'){
        this.taskList.splice(i,1);

      }else{
        var emergency = this.emergency(data.degreeEmergency);
        var type = this.type(data.taskType);
        this.taskList.forEach(element => {
          if(element.id==data.id){
            element.degreeEmergency=data.degreeEmergency;
            element.emergency=emergency;
            element.taskType=data.taskType;
            element.type=type;
            element.startTime=data.startTime;
            element.endTime=data.endTime;
            element.remark=data.remark;
            return;
          }
        });
      }
      
      console.log(data);
    })
    taskEditModal.present();
  }
  waitFor() {
    this.taskList = [];
    this.status = '0'
    this.pageNum = 0;
    this.pageSize = 20;
    this.getTaskList();
  }
  doRefresh(refresher) {
    this.taskList = [];
    this.wamings='waitFor';
    this.status = '0'
    this.pageNum = 0;
    this.pageSize = 20;
    this.getTaskList();
    refresher.complete();//加载完成后，关闭刷新

  }
  history() {
    this.taskList = [];
    this.status = '1'
    this.pageNum = 0;
    this.pageSize = 20;
    this.getTaskList();
  }
  taskAddPage() {
    this.navCtrl.push("TaskAddPage");
  }
  wamingDetails() {
    this.navCtrl.push("WamingDetailsPage");
  }
  getTaskList() {
    let param: any = {};
    param.name = this.name;
    param.status = this.status;
    param.pageNum = this.pageNum;
    param.pageSize = this.pageSize;
    param.userId = AppServiceProvider.getInstance().userInfo.userId;
    param.code = AppServiceProvider.getInstance().userInfo.code;
    this.net.httpPost(
      AppGlobal.API.getTaskList,
      param, res => {
        for (let taskList of res) {
          var img = this.siteType(taskList.siteType)
          var emergency = this.emergency(taskList.degreeEmergency);
          var type = this.type(taskList.taskType);
          var statusName = this.statusName(taskList.status);
          taskList['img'] = img;
          taskList['emergency'] = emergency;
          taskList['type'] = type;
          taskList['statusName'] = statusName;
          this.taskList.push(taskList);
        }
        console.log(this.taskList);

      }, error => {
        this.toast(error);
      }
    )
  }
  searchbar() {
    this.taskList = [];
    this.pageNum = 0;
    this.pageSize = 20;
    this.getTaskList();
  }
  doInfinite(infiniteScroll) {
    setTimeout(() => {
      console.log("加载完成，关闭刷新");
      this.pageNum = this.taskList.length;
      this.getTaskList();
      infiniteScroll.complete();//当数据请求完成调用
    }, 500);
  }
  statusName(status) {
    var statusName = '';
    if (status == '0') {
      statusName = '未处理'
    } else if(status=='1') {
      statusName = '已处理'
    }
    return statusName;
  }
  siteType(siteType) {
    var img = ''
    if (siteType == '1') {
      img = './assets/imgs/automatic.png'
    } else if (siteType == '2') {
      img = './assets/imgs/air.png'
    } else if (siteType == '3') {
      img = './assets/imgs/contaminated.png'
    } else {
      img = './assets/imgs/handle.png';
    }
    return img;
  }
  emergency(degreeEmergency) {
    var emergency = ''
    if (degreeEmergency == '0') {
      emergency = '普通不紧急'
    } else if (degreeEmergency == '1') {
      emergency = '重要紧急'
    } else if (degreeEmergency == '2') {
      emergency = '最高级别'
    }
    return emergency;
  }
  type(taskType) {
    var type = '';
    if (taskType == '0') {
      type = '报警任务'
    } else if (taskType == '1') {
      type = '巡检'
    } else if (taskType == '2') {
      type = '维护'
    } else if (taskType == '3') {
      type = '易耗品更换'
    } else if (taskType == '4') {
      type = '仪器校准'
    } else if (taskType == '5') {
      type = '维修'
    } else if (taskType == '6') {
      type = '试剂更换'
    } else if (taskType == '7') {
      type = '废液回收'
    } else if (taskType == '8') {
      type = '其他'
    }
    return type;
  }
}



